export function calculateROI(initialDeposit, yearlyDeposit, desiredReturns, yearsInvested) {
    const roi = (initialDeposit * Math.pow((1 + desiredReturns), yearsInvested)) +
                (yearlyDeposit * (Math.pow((1 + desiredReturns), yearsInvested) - 1) / desiredReturns);

    return {
        roi: roi.toFixed(2),
        yearsInvested: yearsInvested,
    };
}